  constructor(
    private router: Router,
    public languageService: LanguageService,
    private vhImage: VhImage,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private vhAuth: VhAuth,
    private vhQueryCafe: VhQueryCafe,
    private functionService: FunctionService,
  ) {
    this.keySearch = this.router.getCurrentNavigation().extras.state.keySearch;
    this.sortby = this.router.getCurrentNavigation().extras.state.sortby;
  }



  ngOnInit() {
    this.initForm();
  }



  /**
   * khi to form lu cc trng ca nv
   * @example  this.initForm();
   */
  initForm() {
    this.addEmployee = new FormGroup({
      img: new FormControl(null),
      name: new FormControl(null, [Validators.required, Validators.minLength(6)]),
      email: new FormControl(null, Validators.compose([Validators.required, Validators.email])),
      password: new FormControl(null, [Validators.required, Validators.pattern('^(?=.*?[A-Za-z])(?=.*?[0-9]).{8,}$')]),
      gender: new FormControl(null),
      dateofbirth: new FormControl(this.dateofbirth.toISOString()),
      phone: new FormControl(null, [
        Validators.required,
        this.functionService.phoneValidator(() => this.countryCode),
      ]),
      delete_hidden: new FormControl(false),
      address: new FormControl(null),
      datework: new FormControl(this.datework.toISOString()),
      contract: new FormControl(null),
      permission_group: new FormControl(1),
      default_display_page: new FormControl(''),
    });
  }



  getFile() {
    document.getElementById("file-upload").click();
  }



  /** Ly hnh nh t Desktop */
  onUpload(e?) {
    const file = e.target.files[0];
    this.vhImage.getThumbnailFromDesktop(file, 256, 256, "employees", this.addEmployee.value.img || null).then(
      photoURL => {
        this.addEmployee.controls["img"].setValue(photoURL);
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was loaded successfully")), "success-toast");
      },
      () => {
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was failed")), "alert-toast");
      }
    );
  }



  goBack() {
    this.router.navigate(['/cafe/dashboard/management/partner/employee/employee'], {
      state: {
        keySearch: this.keySearch,
        sortby: this.sortby
      }
    });
  }



  ngOnDestroy() {
    if (!this.createProd && this.addEmployee.value['img'])
      this.vhImage.deleteThumbnail(this.addEmployee.value['img']).then(() => { });
  }



  /**
   * x l thm nv ln DB
   * @example this.addNewEmployee()
   */
  addNewEmployee() {
    this.vhComponent.showLoading("").then(() => {
      let info = { ...this.addEmployee.value };
      // x l s in thoi
      const phoneNumberValue = info.phone;
      info.countryCode = this.countryCode;
      info.phone = this.functionService.getNationalPhoneNumber(phoneNumberValue, this.countryCode);
      info.phoneNumber = this.functionService.getInternationalPhoneNumber(phoneNumberValue, this.countryCode);

      //delete info.email; delete info.password;
      this.vhAuth.signUpMember(this.addEmployee.value.email, this.addEmployee.value.password, info.phoneNumber, info)
        .then((response: any) => {
          let value: any = {}
          if (this.addEmployee.value.permission_group != 10) // index khc 10 (nhn vin khc) l kg to quyn cho nhn vin
            value = this.permissionGroup.filter(item => item.index === this.addEmployee.value.permission_group)[0].value // ly value quyn theo index  chn
          value[`${this.vhQueryCafe.getDefaultBranch()._id}_login_branch`] = true //thm chi nhnh ng nhp mc nh cho nhn vin(l chi nhnh hin ti)
          this.vhAuth.updatePermission(response._id, value).then(() => {
            this.createProd = true
            this.vhComponent.hideLoading(0).then(() => this.goBack())
            this.vhComponent.showToast(2000, this.languageService.translate('Your account employee has been added.'), "success-toast");
          }, err => {
            console.log(err)
            this.vhComponent.hideLoading(0).then(() => {
              this.vhComponent.showToast(1500, this.languageService.translate("An error occurred while adding employee. Please try again"), "alert-toast")
            })
          })
        }, error => {
          console.error(error)
          this.vhComponent.hideLoading(0).then(() => {
            this.vhComponent.showToast(1500, this.languageService.translate("An error occurred while adding employee. Please try again"), "alert-toast")
          })
        })
    })
  }



  /** M quc gia */
  countryCode: string = this.functionService.defaultCountryCode;
  /** Calling code */
  callingCode: string = this.functionService.defaultCallingCode;
  /** S in thoi  format. VD: 333 333 333 */
  formattedPhone: string = '';

  /**
   * M modal chn m quc gia
   */
  openSearchCountryCode() {
    this.functionService.openSearchCountryCode().then((data) => {
      this.countryCode = data.code;
      this.callingCode = data.callingCode;
      this.formattedPhone = this.functionService.getFormattedPhoneNumber(this.addEmployee.value.phone, this.countryCode as CountryCode);
      this.addEmployee.get('phone')?.markAsTouched();
      this.addEmployee.get('phone')?.markAsDirty();
      this.addEmployee.get('phone')?.updateValueAndValidity();
    }).catch((error) => {
      console.error('Error opening country code search:', error);
    });
  }



  /**
   * Format s in thoi khi nhp v set vo form
   * @param event S kin khi nhp s in thoi
   */
  formatPhoneNumber(event: Event): void {
    // Ly s in thoi  format
    const input = (event.target as HTMLInputElement).value;
    this.formattedPhone = this.functionService.getFormattedPhoneNumber(input, this.countryCode as CountryCode);
    // Cp nht vo form
    this.addEmployee.get('phone')?.setValue(this.formattedPhone, { emitEvent: false });
  }

