import { Component, Input, OnInit, SimpleChange } from '@angular/core';
import { VhAuth } from 'ionic-vhframeworks';

@Component({
  selector: 'app-permission',
  templateUrl: './permission.component.html',
  styleUrls: ['./permission.component.scss']
})
export class PermissionComponent implements OnInit {
  permission: any = {}
  freeAccount: any;
  @Input() id_employee: any
  @Input() old_permission_group: any
  visibleLogin: boolean = false; //bin c show/hide phn quyn ng nhp
  visibleSale: boolean = false; //bin c show/hide phn quyn bn hng
  visibleWarranty: boolean = false; //bin c show/hide phn quyn bo hnh, sa cha
  visibleDelivery: boolean = false; //bin c show/hide phn quyn giao hng
  visibleCashflow: boolean = false; //bin c show/hide phn quyn dng tin
  visibleInvoice: boolean = false; //bin c show/hide phn quyn ha n
  visibleReport: boolean = false; //bin c show/hide phn quyn bo co
  visibleConfig: boolean = false; //bin c show/hide phn quyn cu hnh
  visibleManage: boolean = false; //bin c show/hide phn quyn qun l
  visibleNotifications: boolean = false; //bin c show/hide phn quyn thng bo
  visibleBranch: boolean = false //bin c show/hide phn quyn chi nhnh
  visibleAdmin: boolean = false //bin c show/hide phn quyn admin
  constructor(private vhAuth: VhAuth) { }

  ngOnInit() {
    this.freeAccount = this.vhAuth.getStore().freeaccount
    this.getData()
  }

  getData() {
    this.vhAuth.getPermission(this.id_employee)
      .then((permission) => {
        if (permission)
          this.permission = permission;



      })
  }
  ngOnChanges(change: SimpleChange) {
    if (change['old_permission_group']) this.getData()
  }
  updatePermission(value) {
    this.vhAuth.updatePermission(this.id_employee, value)
  }

}
